<template>
    <div id="pinglunList">
        <div class="Newheader">
            <div>最新评论</div>
        </div>
        <ul class="lists">
            <li class="items" v-for="(item,index) in pinglunlist">
                <div class="userSrc">
                </div>
                <ul class="item">
                    <li class="username">
                        {{item.username}}
                    </li>
                    <li class="content">
                        {{item.text}}
                    </li>
                </ul>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "pinglunlist",
        props:{
            pinglunlist:{
                type:Array
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
#pinglunList{
    .Newheader{
        padding-left:20px;
        div{
            color: #e74851;
            position:relative;
            &:before{
                display: block;
                content: '';
                position: absolute;
                width: 4px;
                height: 16px;
                background:#e74851 ;
                border-radius:3px ;
                left: -10px;
                top: 0px;
             }
        }
    }
    .lists{
        margin-top:30px;
        .items{
            position: relative;
            padding-left: 100px;
            min-height:110px;
            margin-bottom:10px;
            padding-bottom: 8px;
            border-bottom:1px dashed #ccc;
            .userSrc{
                width: 80px;
                height: 80px;
                position: absolute;
                background: green;
                border-radius:50% ;
                left: 10px;
            }
            .item{
                .username{
                    color: #e74851;
                    font-size: 13px;
                    margin-bottom: 10px;
                }
                .content{
                    text-indent: 2em;
                    font-size: 15px;
                }
            }
        }
    }
}
</style>


