import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home.vue'
import jishu from '@/components/jishu/jishu.vue'
import artcle from '@/components/artcle/artcle.vue'
import manshenghuo from '@/components/manshenghuo/manshenghuo.vue'
import personal from '@/components/personal/personal.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
        path: '/artcle',
        name: 'artcle',
        component: artcle
    },
      {
          path: '/personal',
          name: 'personal',
          component: personal
      },
      {
          path: '/jishu',
          name: 'jishu',
          component: jishu

      },
      {
          path: '/manshenghuo',
          name: 'manshenghuo',
          component: manshenghuo
      },
    {
      path: '/',
        redirect:'/home'
    }
  ],
  scrollBehavior (to, from, savedPosition) { //路由返回顶部
      return { x: 0, y: 0 }
  }
})



