<template>
    <div class="animated zoomIn" id="jishu">
        <timeArt :timeart="manshenghuo"></timeArt>
        <loadmore @loadmore="loadMore" :nomore="nomore"></loadmore>
    </div>
</template>

<script>
    import loadmore from '../loadmore/loadmore.vue'
    import timeArt from '../timeArt/timeArt.vue'
    export default {
        name: "manshenghuo",
        components:{
            timeArt,
            loadmore
        },
        data(){
            return{
                moreNum:0,
                nomore:false,
                manshenghuo:[]
            }
        },
        props:{
            home:{
                type:Object
            }
        },

        created(){
            var _this=this;
            this.$http.post('/api/manshenghuo',{moreNum: 0}).then(data=>{
                var data=data.body;
                _this.manshenghuo=data
            })
        },
        activated(){
            var _this=this;
            this.$root.eventHub.$on('shuaxin',(param)=>{
                _this.$http.post('/api/manshenghuo',{moreNum: 0}).then(data=>{
                    var data=data.body;
                    _this.manshenghuo=data
                })
            })
        },
        methods:{
            loadMore(){
                var _this=this;
                _this.moreNum++
                this.$http.post('/api/manshenghuo',{moreNum: _this.moreNum}).then(data=>{
                    var data=data.body;
                    if(data.length==0){
                        _this.nomore=true
                    }else{
                        _this.manshenghuo=_this.manshenghuo.concat(data)
                    }
                })
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
    #jishu{
        width: 666px;
        margin: 0 auto;
    }
</style>


