<template>
    <div id="header">
        <ul class="lists">
            <li>
                <router-link to="/home">主页</router-link>
            </li>
            <li>
                <router-link to="/jishu" >技术研究</router-link>
            </li>
            <li>
                <router-link to="/manshenghuo">慢生活</router-link>
            </li>
        </ul>
        <div id="loginWrapper">
            <div v-if="loginSuccess">
                <div class="login">
                    <div class="welcome">
                        <router-link to="/personal">
                            欢迎您,<span>{{userName}}</span>
                        </router-link>

                    </div>
                    <div class="logout" @click="logout">
                        <span class="iconfont icon-logout"></span> 退出
                    </div>
                </div>
            </div>
            <div v-else>
                <div class="login" @click="showlogin">
                    <span class="iconfont icon-login"></span>登录
                </div>
                <div class="zhuche">
                    还没有账号？ <span @click="showZhuce">点我注册</span>
                </div>
            </div>
        </div>
        <transition enter-active-class="animated bounceInDown"
                    leave-active-class="animated bounceOutUp">
            <div id="loginProp" v-show="showLogin">
                <div class="login">
                    <div class="close" @click="closelogin">
                        <span class=" iconfont icon-guanbi"></span>
                    </div>
                    <ul>
                        <li class="loginHeader">
                            <span>用户登录</span>
                        </li>
                        <li class="account">
                            <span class="iconfont icon-account"></span><input @change="account" type="text" name="account" v-model="loginText.account">
                        </li>
                        <li class="password">
                            <span class="iconfont icon-mima"></span><input @change="password" type="password" name="password" v-model="loginText.password">
                        </li>
                        <li class="Btn">
                            <button class="loginBtn"  @click="login">
                                登录
                            </button>
                            <div class="zhuceBtn" @click="showZhuce">
                                 用户注册
                            </div>
                        </li>
                    </ul>
                    <div class="tip" v-show="loginError">
                        {{errorText}}
                    </div>
                </div>
            </div>
        </transition>

        <transition enter-active-class="animated bounceInDown"
                    leave-active-class="animated bounceOutUp">
            <div id="zhuceProp" v-show="showzhuce">
                <div class="login">
                    <div class="close" @click="closezhuce">
                        <span class=" iconfont icon-guanbi"></span>
                    </div>
                    <ul>
                        <li class="loginHeader">
                            <span>用户注册</span>
                        </li>
                        <li class="account">
                            <span class="iconfont icon-account"></span><input placeholder="请输入用户名" @change="zhuceaccount" type="text" name="account" v-model="zhuceText.account">
                        </li>
                        <li class="password">
                            <span class="iconfont icon-mima"></span><input placeholder="请输入密码" @change="zhucepassword" type="password" name="password" v-model="zhuceText.password">
                        </li>
                        <li class="password">
                            <input @change="zhucename" placeholder="请输入昵称" type="text" name="text" v-model="zhuceText.name">
                        </li>
                        <li class="Btn">
                            <button class="loginBtn" @click="zhuce">
                                立即注册
                            </button>
                        </li>
                    </ul>
                    <div class="tip" v-show="loginError">
                        {{errorText}}
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    const ERROR=0;
    import status from '../../common/status'
    export default {
        name: "myheader",
        data(){
            return{
            loginText:{},
            zhuceText:{},
            showLogin:false,
            showzhuce:false,
            loginError:false,
            errorText:'',
            loginSuccess:status.get('userName')? true:false,
            userName:status.get('userName')? decodeURIComponent(status.get('userName')):''
            }
        },
        methods:{
            showlogin(){
                this.showLogin=true
            },
            closelogin(){
                this.showLogin=false
            },
            showZhuce(){
                this.showzhuce=true;
                this.showLogin=false
            },
            closezhuce(){
                this.showzhuce=false
            },
            login(){
               if(!this.loginError&&this.loginText.password&&this.loginText.account){
                  this.$http.post('/api/login',{loginInfo:this.loginText}).then(res => {
                      var data=res.body;
                      if(data.status==ERROR){
                          this.loginError=true;
                          this.errorText='用户名或密码错误，请重新输入';
                          this.loginText={};
                      }
                      else{
                          this.loginSuccess=true;
                          this.userName=data.userInfo.name;
                          status.set('userName',this.userName,3);
                          status.set('userid',data.userInfo.id,3);
                          this.showLogin=false
                      }
                  })
               }
               else{
                   this.loginError=true;
                   this.errorText='用户名或密码为空'
               }
            },
            account(){
                var reg=/\w{7,20}/;
                var account=this.loginText.account;
                if(!(reg.test(account))){
                    this.loginError=true;
                    this.errorText='用户名有误，请重新输入'
                }
                else{
                    this.loginError=false;
                }
            },
            password(){
                var reg=/\w{6,20}/;
                var password=this.loginText.password;
                if(!(reg.test(password))){
                    this.loginError=true;
                    this.errorText='密码有误，请重新输入'
                }
                else{
                    this.loginError=false;
                }

            },
            zhuce(){
                if(!this.loginError){
                    this.$http.post('/api/zhuce',{zhuceInfo:this.zhuceText}).then(res => {
                        var data=res.body;
                        if(data.status==ERROR){
                            this.loginError=true;
                            this.errorText='注册信息有误';
                        }
                        else{
                            this.loginSuccess=true;
                            this.userName=data.userInfo.name;
                            status.set('userName',this.userName,3)
                            status.set('userid',data.userInfo.id,3);
                            this.showzhuce=false
                        }
                    })
                }
                else{
                    this.loginError=true;
                    this.errorText='注册信息有误'
                }
            },
            zhuceaccount(){
                var reg=/\w{7,20}/;
                var account=this.zhuceText.account;
                if(!(reg.test(account))){
                    this.loginError=true;
                    this.errorText='用户名不得低于7位'
                }
                else{
                    this.loginError=false;
                }
            },
            zhucepassword(){
                var reg=/\w{6,20}/;
                var password=this.zhuceText.password;
                if(!(reg.test(password))){
                    this.loginError=true;
                    this.errorText='密码不得低于6位'
                }
                else{
                    this.loginError=false;
                }

            },
            zhucename(){
                var reg=/\w{1,}/;
                var name=this.zhuceText.password;
                if(!(reg.test(name))){
                    this.loginError=true;
                    this.errorText='昵称不能为空'
                }
                else{
                    this.loginError=false;
                }
            },
            logout(){
                status.remove('userName');
                status.remove('userid');
                this.loginSuccess=false;
            }
        }
    }
</script>

<style lang="less" rel='stylesheet/less' scoped>
#header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    background: #222;
    border-bottom: #464647 1px solid;
    .lists{
        display: flex;
        justify-content: center;
        align-items:center;
        height: 70px;
        li{
            text-align: center;
            flex: 0 100px;
            margin-left:10px;
            a{
                display: block;
                color: #fafafa;
                cursor: pointer;
                height: 40px;
                line-height: 40px;
                font-size: 13px;
                border-radius: 6px;
                &.router-link-active{
                    background: #171717;
                    box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 1px rgba(0,0,0,.7);
                }
                &:hover{
                    background: rgba(0,0,0,.2);
                }
            }
        }
    }
#loginWrapper{
    position: absolute;
    right: 0;
    top: 0;
    height: 70px;
    width: 150px;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    color: #fafafa;
    .login{
        font-size: 15px;
        margin-right: 20px;
        flex: 0 135px;
        text-align: center;
        margin-top: 10px;
        cursor:pointer;
        .icon-login{
            margin-right: 3px;
            font-size: 14px;
            color: #339433;
        }
        .logout{
            margin-top: 10px;
            color: red;
        }
        .welcome{
            cursor: pointer;
            font-size:13px;
            span{
                font-size:14px;
                color: #3399ff;
            }
        }
    }
    .zhuche{
        font-size: 12px;
        flex: 0 170px;
        margin-top: 10px;
        span{
            cursor:pointer;
            font-size: 15px;
            color: #336666;
        }
    }
}
#loginProp{
    position: fixed;
    width:100%;
    left: 0;
    top: 0;
    height:100%;
    z-index:100;
    background:rgba(0,20,0,.2);
    .login{
        position: absolute;
        width: 400px;
        left: 50%;
        top: 50%;
        padding-bottom: 10px;
        margin:-150px 0px 0px -200px;
        background: #666;
        box-shadow: inset 0px 2px 1px #000,0px 2px 1px #fff;
        border-radius: 10px;
        .close{
            position: absolute;
            right: -15px;
            top: -15px;
            span{
                font-size: 30px;
                color: #ff7223;
                background: #333;
                border-radius: 50%;
                cursor: pointer;
            }
        }
        .tip{
            width: 100%;
            position: absolute;
            bottom: 8px;
            font-size: 12px;
            text-align: center;
            color: #ee0000;
        }
        ul{
            input{
                text-indent: 27px;
                outline: none;
                background: #666;
            }
            input:-webkit-autofill {
                z-index: 100;
                box-shadow: 0 0 0px 1000px #666 inset;
            }
            .loginHeader{
               font-size: 23px;
               text-align: center;
               background: #ff7223;
               padding: 10px 0px;
               color: #fff;
               border-radius: 10px 10px 0px 0px;
           }
           .account,.password{
               width: 300px;
               height: 40px;
               margin: 20px auto 0px auto;
               border-bottom:1px solid #444;
               position:relative;
               .iconfont{
                   font-size: 24px;
                   color: white;
                   position: absolute;
                   left: 0;
                   bottom: 6px;

               }
               input{
                   width: 100%;
                   height: 100%;
                   color: #fafafa;
                   font-size: 20px;
               }
           }
            .Btn{
                margin: 40px auto 30px auto;
                width: 90px;
                text-align: center;
                position: relative;
                .loginBtn{
                    width: 90px;
                    background: #3879d9;
                    height: 30px;
                    border: none;
                    outline: none;
                    line-height: 30px;
                    border-radius: 6px;
                    color: #fafafa;
                    font-size: 13px;
                    box-shadow: 0 0 2px #fafafa;
                    cursor: pointer;
                }
                .zhuceBtn{
                    position: absolute;
                    color: #fafafa;
                    width: 130px;
                    font-size: 13px;
                    line-height: 30px;
                    right: -130px;
                    top: 0;
                    cursor: pointer;
                }
            }
        }
    }
}
    #zhuceProp{
        position: fixed;
        width:100%;
        left: 0;
        top: 0;
        height:100%;
        z-index:100;
        background:rgba(0,20,0,.2);
        .login{
            position: absolute;
            width: 400px;
            left: 50%;
            top: 50%;
            padding-bottom: 10px;
            margin:-150px 0px 0px -200px;
            background: #666;
            box-shadow: inset 0px 2px 1px #000,0px 2px 1px #fff;
            border-radius: 10px;
            .close{
                position: absolute;
                right: -15px;
                top: -15px;
                span{
                    font-size: 30px;
                    color: #ff7223;
                    background: #333;
                    border-radius: 50%;
                    cursor: pointer;
                }
            }
            .tip{
                width: 100%;
                position: absolute;
                bottom: 8px;
                font-size: 12px;
                text-align: center;
                color: #ee0000;
            }
            ul{
                input{
                    text-indent: 27px;
                    outline: none;
                    background: #666;
                }
                input:-webkit-autofill {
                    z-index: 100;
                    box-shadow: 0 0 0px 1000px #666 inset;
                }
                .loginHeader{
                    font-size: 23px;
                    text-align: center;
                    background: #ff7223;
                    padding: 10px 0px;
                    color: #fff;
                    border-radius: 10px 10px 0px 0px;
                }
                .account,.password{
                    width: 300px;
                    height: 40px;
                    margin: 20px auto 0px auto;
                    border-bottom:1px solid #444;
                    position:relative;
                    .iconfont{
                        font-size: 24px;
                        color: white;
                        position: absolute;
                        left: 0;
                        bottom: 6px;

                    }
                    input{
                        width: 100%;
                        height: 100%;
                        color: #fafafa;
                        font-size: 20px;
                    }
                }
                .Btn{
                    margin: 40px auto 30px auto;
                    width: 90px;
                    text-align: center;
                    position: relative;
                    .loginBtn{
                        width: 90px;
                        background: #3879d9;
                        height: 30px;
                        border: none;
                        outline: none;
                        line-height: 30px;
                        border-radius: 6px;
                        color: #fafafa;
                        font-size: 13px;
                        box-shadow: 0 0 2px #fafafa;
                        cursor: pointer;
                    }
                    .zhuceBtn{
                        position: absolute;
                        color: #fafafa;
                        width: 130px;
                        font-size: 13px;
                        line-height: 30px;
                        right: -130px;
                        top: 0;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
</style>


