<template>
   <div class="animated zoomIn">
      <div v-if="artcle.pinglun" class="artcle">
         <h1 class="title">
            {{artcle.title}}
         </h1>
         <div class="des">
            这篇文章由 <span class="user">{{artcle.username}}</span> 发布于 <span class="time">{{artcle.time-0 | formatDate}}</span>
         </div>
         <div class="content">
            <span v-html="artcle.text"></span>
         </div>

         <div class="artpinglun">
            <div class="header">
               文章评论
            </div>
            <div class="textarea">
               <textarea maxlength="2000" v-model="pinglunText"></textarea>
            </div>
            <div class="pinglunBtn">
               <div class="Btn" @click="pinglun">{{pinglunBtn}}</div>
            </div>
            <div class="pinglunTitle">
               <div class="titlepinglun">
                  评论
               </div>
               <div class="tongji">
                  <i>{{artcle.pinglun.length}}</i>条评论
               </div>
            </div>
            <div>
               <pinglunlist :pinglunlist="artcle.pinglun"></pinglunlist>
            </div>
         </div>

      </div>
   </div>
</template>

<script>
    import {formatDate} from '../../common/date.js';
    import pinglunlist from '../pinglunlist/pinglunlist.vue';
    import status from '../../common/status'
    export default {
        name: "artcle",
        filters: {
            formatDate(time) {
                var date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
        },
        data(){
          return{
              artid:0,
              artcle:{},
              pinglunText:'',
              pinglunBtn:'点评一下'
          }
        },
         components:{
             pinglunlist
         },
        activated(){
            this.artid=this.$route.query.artid;
            var _this=this
            this.$http.post('/api/artcle',{artid:_this.artid}).then(data=>{
                var data=data.body;
                _this.artcle=data[0]
            })
        },
        methods:{
            pinglun(){
               if(status.get('userName')){
                   this.pinglunBtn='点评一下'
                   var userid=status.get('userid');
                   var username=decodeURIComponent(status.get('userName'));
                   var time=new Date().getTime();
                   var _this=this;
                   this.$http.post('/api/pinglun',{artcleId:_this.artid,text:_this.pinglunText,userid:userid,username:username,time:time}).then(data=>{
                       this.$http.post('/api/artcle',{artid:_this.artid}).then(data=>{
                           var data=data.body;
                           _this.artcle=data[0];
                           _this.pinglunText=''
                       })
                   })

               }
               else{
                  this.pinglunBtn='请先登录'
               }
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
.artcle{
   width: 1000px;
   margin: 0 auto;
   color: #fafafa;
   .title{
      font-size: 28px;
      padding: 10px;
      color: white;
      text-align: center;
   }
   .des{
      font-size: 15px;
      .user{
         color: #81a043;
      }
      .time{
         font-size: 13px;
         margin: 2px 0px 0px 4px;
         color: #fffd06;
      }
   }
   .content{
      padding: 10px 0px;
   }
   .artpinglun{
      background: #fafafa;
      border-radius: 10px;
      color: #111;
      padding: 10px 5px;
      .header{
         color: #3399ff;
         border-bottom:2px solid #000;
         padding: 10px 0px;
         text-indent: 8px;
         font-size: 14px;
         font-weight: bold;
      }
      .textarea{
         border: 2px solid #fae765;
         margin-top: 10px;
         border-radius:10px;
         text-align:center;
         textarea{
            width: 99%;
            margin-top: 2px;
            height: 190px;
            outline: none;
            resize: none;
            font-size: 20px;
            border: none;
         }
      }
      .pinglunBtn{
         padding:20px 0px;
      &:after{
          content: '';
          display: block;
          clear: both;
       }
      .Btn{
         float: right;
         background: #3399ff;
         width: 100px;
         height: 30px;
         text-align: center;
         line-height: 30px;
         border-radius: 6px;
         font-size: 13px;
         color: white;
         cursor: pointer;
      }
      }
      .pinglunTitle{
         position:relative;
         height:50px;
         margin-bottom:20px;
         border-bottom:1px solid red;
         .titlepinglun{
            border: 1px solid red ;
            position: absolute;
            width: 100px;
            height: 30px;
            text-align: center;
            line-height: 30px;
            border-radius: 10px 10px 0px 0px;
            border-bottom: 2px  solid white;
            bottom: -1px;
            left: 0px;
            font-weight: bold;
            font-size: 16px;
         }
         .tongji{
            float: right;
            line-height:60px;
            margin-right: 20px;
            color:red;
            i{
               font-size: 22px;
               margin-right: 3px;
            }
         }
      }
   }
}
</style>


