<template>
    <div class="animated zoomIn" id="jishu">
        <timeArt :timeart="jishu" :timeartType="1"></timeArt>
        <loadmore @loadmore="loadMore" :nomore="nomore"></loadmore>
    </div>
</template>

<script>
    import loadmore from '../loadmore/loadmore.vue'
    import timeArt from '../timeArt/timeArt.vue'
    export default {
        name: "jishu",
        components:{
            timeArt,
            loadmore
        },
        data(){
            return{
                moreNum:0,
                nomore:false,
                jishu:[]
            }
        },
        created(){
            var _this=this;
            this.$http.post('/api/jishu',{moreNum: 0}).then(data=>{
            var data=data.body;
                _this.jishu=data
            })
        },
        methods:{
            loadMore(){
                var _this=this;
                _this.moreNum++
               this.$http.post('/api/jishu',{moreNum: _this.moreNum}).then(data=>{
                    var data=data.body;
                    if(data.length==0){
                        _this.nomore=true
                    }else{
                        _this.jishu=_this.jishu.concat(data)
                    }

               })
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
#jishu{
    width: 666px;
    margin: 0 auto;
}
</style>


