<template>
  <div id="app">
    <myheader/>
    <div id="content">
      <div class="router" :style="{ minHeight:routerHeight+ 'px' }">
        <keep-alive>
          <router-view :home="home"/>
        </keep-alive>
      </div>
      <myfooter ></myfooter>
    </div>
    <transition leave-active-class="animated fadeOutUpBig">

    </transition>
    <div id="welcomeMask" v-show="showWelcome">
      <img src="./carloading.gif"  style="width: 300px;">
    </div>
  </div>
</template>
<script>
    import myheader from './components/header/myheader.vue'
    import myfooter from './components/footer/myfooter.vue'
export default {
    name: 'App',
    components:{
        myheader,
        myfooter
    },
    data(){
        return{
            home:{},
            showWelcome:true,
            welcomeInfo:{},
            routerHeight:null
        }
    },
    created(){
        this.routerHeight=document.documentElement.clientHeight;
        this.$http.get('/api/home').then(res => {
            var data= res.body
            this.home=data
            this.showWelcome=false;
            console.log(data)
        })
    },
    watch:{
        '$route':'gethome'
    },
    methods:{
        gethome(){
            this.$http.get('/api/home').then(res => {
                var data= res.body
                this.home=data
            })
        }
    }
}
</script>

<style lang="less" rel='stylesheet/less'>
#content{
    width: 100%;
    min-width: 1000px;
    background: #333;
    z-index: -1;
    .router{
        padding-top: 71px;
        padding-bottom: 20px;
    }

}
#welcomeMask{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #000000;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



