<template>
    <div id="person" style="height: 100%;">
        <ul style="margin-top: 10px;">
            <li class="title">
                <input type="text" placeholder="标题" v-model="title">
            </li>
            <li class="arttype">
                <span><input type="radio" value="1" id="jishu" v-model="arttype" name="arttype"><label for="jishu">技术</label> </span>
                <span><input type="radio" value="2" v-model="arttype" id="manshenghuo" name="arttype"><label for="manshenghuo">慢生活</label></span>
            </li>
            <li class="des">
                <div>文章描述</div>
                <textarea v-model="des"></textarea>
            </li>
            <li>
                <editor id="editor_id" height="500px"  width="1000px" :content="editorText"
                        pluginsPath="/static/kindeditor/plugins/"
                        :loadStyleMode="false"
                        @on-content-change="onContentChange"
                        :uploadJson="uploadJson"
                        :resizeType="resizeType"
                ></editor>
            </li>
            <li class="Btn">
                <span @click="fabiao">
                    {{pinglunBtn}}
                </span>
            </li>
        </ul>
    </div>
</template>

<script>
    import status from '../../common/status.js'
    export default {
        name: "personal",
        data () {
            return {
                editorText: '',
                title:'',
                des:'',
                arttype:1,
                uploadJson:'/api/upload',
                resizeType:0,
                pinglunBtn:'发表'
            }
        },
        created () {
        },
        methods: {
            onContentChange (val) {
                this.editorText = val
            },
            fabiao(){
                if(status.get('userName')){
                    this.pinglunBtn='发表'
                    var userid=status.get('userid');
                    var username=decodeURIComponent(status.get('userName'));
                    var time=new Date().getTime();
                    var _this=this;
                    if(_this.title==0){
                        alert('请输入标题')
                    }else{
                        this.$http.post('/api/fabiao',{zhan:0,title:_this.title,text:_this.editorText,des:_this.des,arttype:_this.arttype,userid:userid,username:username,time:time}).then(data=>{
                            _this.$root.eventHub.$emit('shuaxin',null)
                            _this.title='';
                            _this.editorText='';
                            _this.des='';
                            alert('发表成功')
                        })
                    }
                }
                else{
                    this.pinglunBtn='请先登录'
                }
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
#person{
    width: 1000px;
    margin: 0 auto;
    .header{
        text-align: center;
        color: white;
        font-size: 20px;
        padding: 20px 0px;
    }
    .title{
        width: 100%;
        input{
            background: #333;
            outline: none;
            width: 100%;
            text-align: center;
            font-size: 24px;
            height: 30px;
            color: white;
        }
        input::placeholder{
            color: #fafafa;
        }
    }
    .arttype{
        color: white;
        text-align: center;
        margin-top: 10px;
        input{
            vertical-align: bottom;
            margin-right: 2px;
            cursor: pointer;
        }
        span{
            margin-right: 10px;
        }

    }
    .des{
        div{
            color: #fafafa;
            padding: 30px 0px 10px 0px;
            font-size: 20px;
            text-indent: 10px;
        }
        textarea{
            resize: none;
            width: 100%;
            height: 200px;
            font-size: 20px;
            outline: none;
            border-radius: 5px;
        }
    }
    .Btn{
        margin: 20px 0px;
        text-align: center;
        span{
            display: inline-block;
            cursor: pointer;
            width: 90px;
            height: 30px;
            line-height: 30px;
            background: #3399ff;
            border-radius: 4px;
            color: white;
        }
    }
}
</style>


