<template>
    <div v-if="!nomore" id="loadmore" @click="loadmore">
        <span class="iconfont icon-xiala"></span>加载更多
    </div>
    <div v-else style="text-align: center;color: #ff7223;">
        没有更多了.....
    </div>
</template>

<script>
    export default {
        name: "loadmore",
        data(){
            return{

            }
        },
        props:{
            nomore:{

            }
        },
        methods:{
            loadmore(){
                this.$emit('loadmore')
            }
        }
    }
</script>

<style lang="less" rel="stylesheet/less" scoped>
    #loadmore{
        text-align: center;
        color: white;
        cursor: pointer;
        position: relative;
        animation:dong 1s infinite;
        .iconfont{
            font-size: 13px;
            margin-right: 5px;

        }
    }
    @keyframes dong
    {
        0% {top:3px}
        50% {top:0px}
        100%{top:3px}
    }
</style>


